/* webfont import */
@font-face {
  font-family: 'Cafe24Ssurround';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24Ssurround.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Pretendard-Light';
  src: url('./fonts/Pretendard-ExtraLight.woff') format('woff');
}
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('./fonts/Pretendard-Regular.woff') format('woff');
}
@font-face {
  font-family: 'Pretendard-semiBold';
  src: url('./fonts/Pretendard-SemiBold.woff') format('woff');
}
@font-face {
  font-family: 'Pretendard-Bold';
  src: url('./fonts/Pretendard-ExtraBold.woff') format('woff');
}
@font-face {
  font-family: 'TmoneyRoundWindExtraBold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/TmoneyRoundWindExtraBold.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Yoonwoo';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105@1.1/Yoonwoo.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}





*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

html{
  min-height:100%;
  background-color: #e3e6eb;
}

::-webkit-scrollbar {
  display: none;
}

p{
  margin-bottom:0;
}

.App {
  text-align: center;
  background-color: #e3e6eb;
}

/* Header style */
.App-header {
  background-color: #e3e6eb;
  /* width:90%; */
  /* border:1px solid red; */
}
.navBarPC{
  /* border:1px solid blue; */
  background-color: #e3e6eb;
  width:90%;
  margin: 30px auto;
  font-family: 'Cafe24Ssurround', 'Courier New', monospace;
  font-stretch:extra-expanded;
  display: flex;
  justify-content: space-between;
  max-width:1300px;
}
.navContainer{

}
.logo{
  width:90px;
  display: inline-block;

  /* drag prevention */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  /* user-drag: none; */
}
.navBoxPC{
  margin-right:30px;
  display: flex;
  align-items: center;
}
.navLink{
  text-decoration: none;
  color:#8f8f8f;
  margin-right:10px;
}
.navLink:hover{
  color:#405980;
  transition: all 0.3s ease-in-out;
}
.navItem{
}
.navItem:hover{
}
.activeNav{
  color:#405980;
  transition: all 0.3s ease-in-out;
}

/* Mobile Navbar Style */
.mobileNavContainer{
  display: none;
}

.mobileNav{
  /* border:1px solid skyblue; */
  width:100%;
  height:70px;
  background-color: #ffffff;
  position:fixed;
  bottom:0;
  left:0;
  box-shadow: 0px -2px 4px #dbdcde;
  /* border-top:1px solid #ababab; */
  font-family: 'Pretendard-Regular', 'Courier New', monospace;
  display: flex;
  justify-content: center;
  z-index:100;
}
.mNavLink{
  width:25%;
  justify-content: center;
  padding-top:12px;
  /* border:1px solid blue; */
  text-decoration: none;
}
.mNavItem{
  display:flex;
  flex-direction: column;
  justify-content: center;
  padding-top:6px;
  color:#ababab;
}
.mNavIcon{
  font-size: 18px;
}
.mNavText{
  font-size:10px;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
  margin-top:5px;
}
.activeMNav{
  color:#7097d1;
  transition: all 0.3s ease-in-out;
}

.mobileLogo{
  width:50px;
  display: none;
  /* drag prevention */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

/* main style*/
.main{
  width: 90%;
  max-width: 1300px;
  height:auto;
  /* border:1px solid blue; */
  background-color: #f9f9f9;
  margin: 120px auto 30px;
  padding: 50px;
  position:relative;
  border-radius: 15px;
  /* transition: all 2s; */

  font-family: 'Pretendard-Regular', 'Courier New', monospace;
  color:#676767;
}
.circleProfile{
  width:330px;
  height:330px;
  /* border:1px solid purple; */
  border-radius: 50%;
  position:absolute;
  top:-100px;
  left:5%;
  border:15px solid white;
  /* box-shadow: 10px 10px 1.5px #888e98; */
  /* transition: all 0.3s; */
}
.profilePic{
  width:100%;
  height:100%;
  border-radius: 50%;

  /* drag prevention */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

/* Profile Contents Style */
.profileContainer{
  /* border:1px solid red; */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: minmax(150px, auto);
  column-gap: 5px;
  row-gap: 15px;
  width:100%;
  height:auto;
  padding:20px;
}
.profileBox{
  /* border:1px solid blue; */
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.profileBox > p{
  margin-bottom:10px;
  
}

.mobileLine{
  display: none;
}


.box1{
  grid-row: 1/3;
  justify-content: flex-start;
}
.meBox{
  /* border:1px solid mediumaquamarine; */
  width:70%;
  height:auto;
  margin: auto;
  margin-top:200px;
  text-align: left;
}
.meBox > p{
  font-size:90%;
  margin-bottom:6px;
}
.box1Name{
  display: none;
}
.meMail{
  text-decoration: none;
  color:#5f8acb;
  transition: all 0.3s ease-in-out;
}
.linkIcon{
  display: block;
  margin:25px 0;
  color:#b0b0b0;
}
.meA{
  display: block;
  width:fit-content;
  color:#8ac4e6;
  transition: all 0.3s ease-in-out;
  font-size: 75%;
  margin-bottom:5px;
}


.box2{
  border-right: 1px solid #929292;
}
.icon{
  display: inline-block;
  margin-right:10px;
  color:#445671;
}
.gpa{
  font-size:0.75em;
  color:#929292;
}

.box3{
  text-align: left;
  padding-left: 40px;
  font-size:14.5px;
  font-family: 'Pretendard-Light', 'Courier New', monospace;
  line-height: 20px;
  letter-spacing: -0.1px;
}

.box4{
  grid-column: 2/4;
  /* border-top:1px solid #929292; */
  padding: 12px 8px;
}
.expBox{
  font-family: 'Pretendard-Bold', 'Courier New', monospace;
  font-size: 18px;
  color:#556a8a;
  text-transform: uppercase;
}
.expLine{
  display: block;
  width:1px;
  height:20px;
  border-left:1.5px solid #929292;
  margin:5px auto;
}
.jobBox{
  margin: 10px auto;
  /* border:1px solid purple; */
  width:90%;
}
.jobTitle{
  font-family: 'Pretendard-semiBold', 'Courier New', monospace;
  display: inline-block;
  font-size: 18px;
  color:#5d6d87;
  
}
.jobCircleBox{
  display: inline-block;
  color:#929292;
  text-shadow: none;
}
.jobCircle{
  display: inline-block;
  width:6px;
  height:6px;
  border-radius: 50%;
  margin:0 5px 2px 10px;
}
.red{
  background-color: #bc4044;
}
.green{
  background-color: #679261;
}
.jobType{
  display: inline-block;
  font-family: 'Pretendard-Light', 'Courier New', monospace;
  font-size:12.5px;
}
.jobLocation{
  font-size:15px;
}
.jobDuration{
  font-family: 'Pretendard-Light', 'Courier New', monospace;
  font-size:13.5px;
}

.box5{
  grid-column: 1/4;
  margin-top:25px;
  text-align: center;
}
.skBox{
  font-family: 'Pretendard-Bold', 'Courier New', monospace;
  font-size: 18px;
  color:#556a8a;
  text-transform: uppercase;
}
.skLine{
  display: block;
  width:100px;
  height:1px;
  border-bottom:1px solid #929292;
  margin:5px auto;
}
.skCircleBox{
  /* border:1px solid #467ed2; */
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width:80%;
  margin: 25px auto;
}
.skCircle{
  /* width:150px; */
  /* height:150px; */
  font-family: 'Pretendard-Regular', 'Courier New', monospace;
  position: relative;
  /* border:1px solid red; */
  margin: 15px 25px;
}
.indicator{
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  font-size:90%;
}
.indicator>p:first-child{
  margin-bottom:5px;
}

/* Footer style */
.App-footer{
  /* border:1px solid green; */
  width:100%;
  padding: 25px 15px;
  margin-bottom:30px;
  color:#929292;
  font-family: 'Pretendard-Light', 'Courier New', monospace;
}
.App-footer > span{
  display: block;
  font-size:12px;
}
.App-footer > p{
  font-size: 13px;
}




/* Projects Page Style */
.main2{
  width: 90%;
  max-width: 1300px;
  height:auto;
  /* border:1px solid blue; */
  background-color: #f9f9f9;
  margin: 30px auto;
  padding: 50px;
  /* position:relative; */
  border-radius: 15px;
  /* transition: all 2s; */
  margin-top:50px;
}

.projectH2{
  margin-bottom:15px;
  font-size:36px;
  font-family: 'TmoneyRoundWindExtraBold';
  text-transform: uppercase;
  letter-spacing: -1.6px;
  color:#33496b;
  cursor: default;
}
.projectH6{
  margin-bottom:70px;
  font-size: 13px;
  font-family: 'Pretendard-Light';
  cursor: default;
}

.projectContainer{
  display:flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-content: space-around;
}

.projectBox{
  /* border:1px solid purple; */
  height:fit-content;
  width:230px;
  margin:100px 10px 50px 10px;
}
.projectDesc{
  /* border:1px solid red; */
  /* background-color: orange; */
  width:100%;
  height:100px;
  z-index:1;
  position:relative;
  box-shadow: 5px -2px 1px #b7c0ca;
  border-radius: 18px 8px 5px 5px;
}
.projectCircle{
  display: block;
  /* border:1px solid black; */
  /* background-color: black; */
  width:150px;
  margin: auto;
  position:absolute;
  height:150px;
  border-radius:50%;
  z-index:-1;
  top:-65px;
  left:17%;
  /* transform: translate(-50%, 0); */
  transition: all 0.6s ease-in-out;
  background-position: center;
  /* background-repeat: no-repeat; */
  box-shadow: 6px 0px 0px #b7c0ca;
  border:1px solid #bebebe;
}
.innerCircle{
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
  width:33px;
  height:33px;
  background-color: white;
  border-radius: 50%;
  /* opacity:0; */
  box-shadow: 1px 3px 2px rgb(206, 206, 206) inset;
  border:1px solid #aaafb5;

}
.projectDescBox{
  position:absolute;
  z-index: 2;
  width: 100%;
  height:100%;
  background-color: #dbe2ed;
  padding-top:15px;
  border-radius: 18px 8px 5px 5px;
  cursor:pointer;
}
.projectDesc:hover > .projectCircle {
  top:-100px;
  animation: rotate_circle 2.6s ease-in-out infinite;
}
@keyframes rotate_circle{
  100%{
    transform: rotate(360deg);
  }
}
.projectTitle{
  font-family: 'Yoonwoo';
  font-size: 21.5px;
  color:#4e617e;
}
.projectNote{
  display: block;
  margin-top:5px;
  
}
.projectIcon{
  background-color: whitesmoke;
  display: inline-block;
  padding:7px;
  border-radius: 5px;
  font-size: 14px;
}





.modalBg{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.45);
  /* z-index: 1000; */
  display: flex;
  justify-content: center;
  align-items:flex-end;
  z-index:200;
  
}
.modalWindow{
  position:relative;
  /* width:100%;
  height:100%; */

  /* position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%); */
  background-color: white;
  width:70%;
  max-width:1000px;
  height:730px;
  /* color:#408059; */
  padding-bottom:100px;
  padding-top:0;
  box-shadow: 8px -8px 15px rgba(0,0,0,0.3);
  color:#595959;
}

/* .projectModal{
  width:60%;
  margin: 0 auto;
  background-color: #405980;
  height:350px;
} */

.modalWindow > div {
  /* top:0;
  display: flex;
  flex-direction: column;
  align-items: center; */
}
.modalWindow > div > img {
  display: block;
  width: 100%;
  margin: auto;
}
.modalWindow > div > h2{
  font-family: 'Pretendard-Bold';
  font-size: 32px;
}
.modalWindow > div > h3{
  font-size: 20px;
  margin:14px 0;
}
.modalScreenIcon{
  color:#b0b0b0;
  margin: 0 5px;
}
.modalWindow > div > h4{
  font-family: 'Pretendard-Regular';
  font-size: 16px;
  margin-bottom: 15px;
}
.modalWindow > div > h5{
  font-family: 'Pretendard-semiBold';
  font-size: 18px;
  padding: 5px 100px;
}
.modalWindow > div > h6{
  font-family: 'Pretendard-light';
  font-size: 14px;
  margin-top:18px;
}

.modalWindow > div > a{
  font-family: 'Pretendard-light';
  display: block;
  text-decoration: none;
  /* margin-top: 80px; */
  /* border:1px solid red; */
  width:100px;
  margin:50px auto 0;
  padding:10px 20px;
  border-radius: 4px;
  color: white;
  transition: all 0.3s ease-in-out;
}


.modalCloseBtn{
  position: absolute;
  top: -100px;
  border: none;
  background-color: rgba(0,0,0,0);
  color: white;
  opacity: 0.8;
  font-size: 46px;
  left: 47.5%;
  display: block;
  margin:auto;
}


@media (max-width:1200px){
  .profileBox{
    margin-bottom:25px;
  }
  .box1{
    
  }
  .meBox{
    width:90%;
    margin-top: 180px;
  }
  .meBtn{
    display: block;
    margin: 15px 0;
  }

  .box2{
    grid-column: 2/4;
    border-right: none;
  }
  .box3{
    grid-column: 2/4;
    grid-row: 2/3;
    text-align: center;
    border-left: 1px solid #929292;
    padding-left: 0;
  }
  .box4{
    grid-column: 1/4;
  }
  .expLine{
    width:110px;
    height:1px;
    border-left:none;
    border-bottom:1px solid #929292;
    margin:5px auto;
  }
  .box5{
    margin-top:8px;
    margin-bottom:0px;
  }

  .modalWindow{
    height: 700px;
  }
}

@media (max-width: 868px){
  .circleProfile{
    width:280px;
    height:280px;
  }
  .meBox{
    width:100%;
    margin-top:120px;
  }
  .meBox > p {
    font-size:70%;
  }
  .meA{
    font-size:65%;
  }
  .meBtnBox{
    margin-top:20px;
  }
  .meBtn{
    font-size:70%;
  }

  .box2{
    text-align: right;
    padding-right:30px;
    font-size:95%;
  }
  .box3{
    font-size:88%;
  }
  .jobTitle{
    font-size: 95%;
  }
  .jobLocation, .jobDuration{
    font-size:90%;
  }
  .box5{
    margin-bottom:0px;
  }

  .modalWindow{
    width:100%;
    
  }


  
}


/* Media Query for Smaller Devides */
@media (max-width: 668px){

  /* Hide PC Navbar */
  .navBarPC{
    display: none;
  }
  .App-header{
    padding: 30px 30px 10px 45px;
    text-align: left;
  }
  .mobileLogo{
    display: inline-block;
  }

  .mobileNavContainer{
    display: block;
  }

  .main{
    width:95%;
    margin-bottom: 30px;
    margin-top:100px;
  }
  .main2{
    width:95%;
    margin-bottom:30px;
  }
  .circleProfile{
    width:300px;
    height:300px;
    left:50%;
    transform: translate(-50%, 0);
  }

  .profileContainer{
    display: block;
    padding-bottom: 0;
  }
  .profileBox{
    margin: 25px auto;
    min-height:150px;
  }

  .mobileLine{
    display: block;
    width:1px;
    height:30px;
    border-left:1px solid #929292;
    margin: 0 auto;
  }

  .box1{
    margin-top:150px;
  }
  .meBox{
    margin-top:10px;
    text-align: center;
  }
  .meBox > p{
    font-size:16px;
    margin-bottom:16px;
  }
  .linkIcon{
    margin: 35px auto;
  }
  .box1Name{
    display:block;
  }
  .meA{
    font-size:14px;
    margin: 7px auto;
  }
  .meBtnBox{
    font-size:18px;
    margin: 45px auto 0px;
  }
  .meBtn{
    font-size: 80%;
  }

  .box2{
      text-align: center;
      padding-right: 0px;
      min-height:fit-content;
  }
  .box2Name{
    display: none;
  }
  .box3{
    border-left:none;
  }
  .box4{
    border-top:none;
    margin-top:70px;
  }
  .jobCircleBox{
    display: block;
    font-size:60%;
    margin:8px 0 0 0;
  }
  .jobLocation, .jobDuration{
    font-size:70%;
  }
  .box5{
    margin-bottom:0px;
    margin-top:70px;
  }

  .App-footer{
    margin-bottom:80px;
  }
  
  .modalWindow{
    height:530px;
  }
  .modalWindow > div > h2{
    font-size: 28px;
  }
  .modalWindow > div > h3{
    font-size: 18px;
  }
  .modalWindow > div > h4{
    font-size: 12px;
  }
  .modalWindow > div > h5{
    font-family: 'Pretendard-semiBold';
    font-size: 14px;
    padding: 5px 50px;
  }
  .modalWindow > div > h6{

  }
  
  .modalWindow > div > a{
    margin-top:30px;
    width:80px;
    font-size:80%;
  }
  
  
  .modalCloseBtn{
    position: absolute;
    top: -80px;
    border: none;
    background-color: rgba(0,0,0,0);
    color: white;
    opacity: 0.8;
    font-size: 32px;
    left: 47.5%;
    display: block;
    margin:auto;
  }

}





